<template>
  <div class="login_main">
    <div class="solar-syst">
      <div class="sun"></div>
      <div class="mercury"></div>
      <div class="venus"></div>
      <div class="earth"></div>
      <div class="mars"></div>
      <div class="jupiter"></div>
      <div class="saturn"></div>
      <div class="uranus"></div>
      <div class="neptune"></div>
      <div class="pluto"></div>
      <div class="asteroids-belt"></div>
    </div>
    <div class="login-wrap">
      <!-- <div class="ms-title">后台管理系统</div> -->
      <div class="ms-login">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="0px"
          class="demo-ruleForm"
        >
          <el-form-item prop="username">
            <el-input v-model="ruleForm.username" placeholder="username"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              type="password"
              placeholder="password"
              v-model="ruleForm.password"
              @keyup.enter.native="submitForm('ruleForm')"
            ></el-input>
          </el-form-item>
          <!-- <el-form-item prop="code">
            <el-input type="text" v-model="ruleForm.code" placeholder="验证码">
              <template slot="append">
                <div @click="updateCaptcha" class="captcha" v-html="captcha"></div>
              </template>
            </el-input>
          </el-form-item> -->
          <div class="login-btn">
            <el-button type="primary" @click="submitForm()">登录</el-button>
          </div>
          <!-- <p style="font-size:12px;line-height:30px;color:#999;">Tips : 用户名和密码随便填。</p> -->
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { getCaptcha, LoginByUsername } from "@/api";
import http from '@/api/http'
export default {
  data: function() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
        code: ""
      },
      captcha: null,
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    // 获取验证码
    getCaptchas() {
      getCaptcha().then(res => {
        this.captcha = res.data;
        console.log(res.data);
      });
    },
    submitForm() {
      LoginByUsername({
        username: this.ruleForm.username,
        password: this.ruleForm.password,
        code: this.ruleForm.code
      }).then(res => {
        if (res.data.errcode === 1) {
          // sessionStorage.setItem("token", res.data.data.token);
          http.defaults.headers.common["Authorization"] =res.data.data.token;
          sessionStorage.setItem("ms_username", res.data.data.username);
          this.$router.push("/");
        }
      });
    },
    updateCaptcha() {
      this.getCaptchas();
    }
  },
  mounted() {
    this.getCaptchas();
  }
};
</script>

<style scoped>
@import "../assets/css/solar.css";
.login_main {
  width: 100%;
  height: 100%;
  position: relative;
}
.login-wrap {
  position: absolute;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}
.ms-title {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -230px;
  text-align: center;
  font-size: 30px;
  color: #fff;
}
.ms-login {
  position: absolute;
  left: 80%;
  top: 50%;
  width: 300px;
  height: 180px;
  margin: -150px 0 0 -190px;
  padding: 40px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.4);
  box-sizing: initial !important;
}
.login-btn {
  text-align: center;
}
.login-btn button {
  width: 100%;
  height: 36px;
}
</style>