<template>
  <div>
    <input type="file" @change="onChange">
  </div>
</template>

<script>
import {uploadFile} from '@/api'
export default {
  data() {
    return {
      exc_file: {}
    };
  },
  methods: {
    onChange(event) {
      //预览
      var that = this;
      this.exc_file = event.target.files[0];   
      console.log(event.target.files);
      
      if (this.exc_file) {
        var fd = new FormData();
        fd.append("file", this.exc_file);
        uploadFile( fd).then(res => {});
      }
    }
  }
};
</script>

