<template>
  <div class="main_wrap">
    <div class="editor_wrap">
      <div class="title_wrap">
        <div class="title_content">
          <label>标题：</label>
          <el-input v-model="title" placeholder="请输入内容"></el-input>
          <label>简介：</label>
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 6}"
            placeholder="请输入内容"
            v-model="textarea"
          ></el-input>
        </div>
        <div class="tags_content">
          <p>
            <label>标签：</label>
          </p>
          <el-select
            v-model="tags"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="请选择文章标签"
          >
            <el-option
              v-for="item in options5"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
      </div>
      <quill-editor
        v-model="content"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
      <el-button type="primary" @click="saveArticle()">保存</el-button>
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
// require styles
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { saveArticle } from "@/api";
export default {
  data() {
    return {
      title: "",
      textarea: "",
      content: null,
      editorOption: {},
      options5: [
        {
          value: "HTML",
          label: "HTML"
        },
        {
          value: "CSS",
          label: "CSS"
        },
        {
          value: "JavaScript",
          label: "JavaScript"
        }
      ],
      tags: []
    };
  },
  components: {
    quillEditor
  },
  methods: {
    onEditorBlur() {
      //失去焦点事件
      console.log(this.content);
    },
    onEditorFocus() {
      //获得焦点事件
    },
    onEditorChange() {
      //内容改变事件
    },
    saveArticle() {
      saveArticle({
        title: this.title,
        detail: this.textarea,
        content: this.content,
        tags: this.tags.join(",")
      }).then(res => {
        if (res.data.errcode === 1) {
          this.clearAll();
          this.$message({
            message: "文章发布成功！",
            type: "success"
          });
        } else {
          this.$message.error("文章发布失败!");
        }
      });
    },
    // 清除数据
    clearAll() {
      this.title = "";
      this.textarea = "";
      this.content = null;
      this.tags = [];
    }
  }
};
</script>
<style>
.editor_wrap {
  min-height: 100%;
}
.title_wrap {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}
.title_content {
  width: 70%;
  box-sizing: border-box;
  margin-right: 20px;
}
.tags_content {
  flex: 1;
}
.ql-container.ql-snow {
  height: 50vh;
}
</style>
